import React, { useState } from 'react';
import { ethers } from 'ethers'

const IDOModal = ({ open, onClose }) => {
    const [Reply, setReply] = useState("No");
    if (!open) return null;

    const style = {
        maxWidth: '100px',
        marginTop : '2vh',
        height: '5vh',
        marginLeft: '60px',
        flex: '2'
    }
    const style2 = {
        flex: '1',
        marginTop : '2vh',
        marginLeft: '30px',
    }
    return (
        <div onClick={onClose} className='overlay'>
            <div
                onClick={(e) => {
                    e.stopPropagation();
                }}
                className="modalGameContainer"
            >
                <div className='modalRight'>
                    <p className='closeBtn' onClick={() => onClose(null)}>
                        X
                    </p>
                    <h4 align="middle"></h4><br />
                    <div className='inputContainer'>
                        <input
                            autoComplete="off"
                            type="number"
                            id="amount"
                            name="amount"
                            placeholder="10 - 888"
                            style={style}
                        />
                        <div style={style2}>
                            USDT
                        </div>
                    </div>
                    <div className='IDOButtonDiv'>
                        <button id="buttonIDO"> Make IDO</button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default IDOModal;