import React, { useState } from 'react'
import { CopyToClipboard } from 'react-copy-to-clipboard';
import money from '../image/money.png'
import BodyNavbar from './BodyNavbar';
import IDO from './IDO';
import IDOStatus from './IDOStatus';

const Body = ({ defaultAccount, contract }) => {
  let link = "localhost:3000/inviter=";
  const defaultLink = "";
  const [refLink, setRefLink] = useState(defaultLink)
  const [copied, setCopied] = useState(false);

  const page1 = document.getElementById("Page1")
  const page2 = document.getElementById("Page2")
  const page3 = document.getElementById("Page3")
  const page4 = document.getElementById("Page4")

  const alertCopied = () => {
    if (refLink === defaultLink)
      alert(`${defaultLink}`)
    else
      alert(`Your Reflink ${refLink} has been copied`);
  }

  const generateLink = () => {
    if (defaultAccount === defaultLink) return;
    if (refLink !== null) return;
    let tempLink = link.concat(`${defaultAccount}`)
    setRefLink(tempLink)
  }

  const handlePageSelect = (value) => {
    console.log("The value is " + value)
    if (value === 0) {
      page1.hidden = false
      page2.hidden = true
      page3.hidden = true
      page4.hidden = true
    }
    if (value === 1) {
      page1.hidden = true
      page2.hidden = false
      page3.hidden = true
      page4.hidden = true
    }
    if (value === 2) {
      page1.hidden = true
      page2.hidden = true
      page3.hidden = false
      page4.hidden = true
    }
    if (value === 3) {
      page1.hidden = true
      page2.hidden = true
      page3.hidden = true
      page4.hidden = false
    }
  }

  generateLink();

  return (
    <div className='bodyContent'>

      <div className='bodyInviteArea'>
        <div className='bodyInviteAreaLeft'>
          <span id="bodyContentTitle"> 5%  <br /></span>
          <span id="bodyContentContent">  USDT </span>
        </div>
        <div className='bodyInviteAreaRight'>
          <img src={money} alt="money" id="moneyPic" />
        </div>
      </div>

      <div className='bodyNavbarArea'>
        <BodyNavbar
          whichPage={handlePageSelect} />
      </div>

      <div className='bodyAccordingToNav'>
        <div id='Page1'>
          <IDO />
        </div>
        <div id='Page2' hidden>
          <IDOStatus contract={contract}/>
        </div>
        <div id='Page3' hidden>

        </div>
        <div id='Page4' hidden>

        </div>
      </div>
{/*       
      <CopyToClipboard text={refLink} onCopy={() => setCopied(true)}>
        <button id="inviteLink" onClick={alertCopied}>
          Copy Invite Link
        </button>
      </CopyToClipboard> */}
    </div>
  )
}

export default Body