import React, { useState } from 'react'

const IDOStatus = ({ contract }) => {
    const [IDOTotal, setIDOTotal] = useState(0);
    const [ContributorTotal, setContributorTotal] = useState(0);

    const getStatus = async () => {
        if (contract === null) return;
    }

    return (
        <div className='statusPage'>
            <div className='statusLeft'>
                --Total--<br />
                IDO Total :<br />
                IDO Contributor Total :<br /><br />

                --Personal--<br />
                IDO Amount :<br />
                Reward from Referrals :<br />
                Referrals :<br />
            </div>
            <div className='statusRight'>
                <br />
                {IDOTotal}<br />
                {ContributorTotal}<br /><br />
            </div>
        </div>
    )
}

export default IDOStatus
