import React, { useState } from 'react'
import IDOImage from '../image/TuYuanYuanIDO2.png'
import IDOModal from './IDOModal'
const IDO = () => {
    const [modalIsOpen, setModalIsOpen] = useState(false);

    const style = {
        position: 'absolute',
        // backgroundColor: 'transparent',
        color: '#efc75e',
        width: '40vw',
        height: '5vh',
        left: '30vw',
        top: '45vh',
        zIndex: '5'
    }

    const imageStyle = {
        width: '100vw'
    }

    const handleModalClose = () => {
        setModalIsOpen(false)
    }

    const makeIDO = () => {

    }
    return (
        <div className='IDOPage'>
            <img src={IDOImage} alt="IDOImage" style={imageStyle} onClick={()=>setModalIsOpen(true)}/>
            {/*  TuYuanYuan IDO<br />
             10 - 888 USDT<br /> */}
            {/* <button id="buttonIDO">  IDO </button> */}
            {/* <input
                autoComplete="off"
                type="number"
                id="amount"
                name="amount"
                placeholder="10 - 888"
                style={style}
            /><br /> */}
            <div className='IDOModal'>
                <IDOModal
                    open={modalIsOpen}
                    onClose={handleModalClose}
                />
            </div>
        </div>
    )
}

export default IDO
